const handleMobileSelect = (key) => {
  const path = languageStore.getLocalizedPath(`/${key}`)
  router.push(path)
  // ÈÄâÊã©ÂêéÂÖ≥Èó≠ÁßªÂä®Á´ØËèúÂçï
