          <el-icon><Tools /></el-icon>
        </div>
      </template>

      <div class="debug-content">
        <el-button type="primary" @click="runDiagnosis" :loading="loading">
          ÂºÄÂßãËØäÊñ≠
        </el-button>

        <div v-if="diagnosisResult" class="diagnosis-result">
          <el-divider>ËØäÊñ≠ÁªìÊûú</el-divider>
          
          <el-tabs v-model="activeTab">
            <el-tab-pane label="ÊµèËßàÂô®ÊîØÊåÅ" name="browser">
              <el-descriptions :column="2" border>
                <el-descriptions-item label="ÂÆâÂÖ®‰∏ä‰∏ãÊñá">
                  <el-tag :type="diagnosisResult.browser.isSecureContext ? 'success' : 'danger'">
                    {{ diagnosisResult.browser.isSecureContext ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}
                  </el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="ÂçèËÆÆ">
                  {{ diagnosisResult.browser.protocol }}
                </el-descriptions-item>
                <el-descriptions-item label="FetchÊîØÊåÅ">
                  <el-tag :type="diagnosisResult.browser.features.fetch ? 'success' : 'danger'">
                    {{ diagnosisResult.browser.features.fetch ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}
                  </el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="CORSÊîØÊåÅ">
                  <el-tag :type="diagnosisResult.browser.features.cors ? 'success' : 'danger'">
                    {{ diagnosisResult.browser.features.cors ? 'ÊîØÊåÅ' : '‰∏çÊîØÊåÅ' }}
                  </el-tag>
                </el-descriptions-item>
              </el-descriptions>
            </el-tab-pane>

            <el-tab-pane label="ËøûÊé•ÊµãËØï" name="connection">
              <div v-for="(test, key) in diagnosisResult.tests" :key="key" class="test-result">
                <h4>{{ getTestName(key) }}</h4>
                <el-alert
                  :title="test.success ? 'ËøûÊé•ÊàêÂäü' : 'ËøûÊé•Â§±Ë¥•'"
                  :type="test.success ? 'success' : 'error'"
                  :closable="false"
                  show-icon
                >
                  <template #default>
                    <div v-if="test.success">
                      <p><strong>Áä∂ÊÄÅÁ†Å:</strong> {{ test.details.status }}</p>
                      <p><strong>Áä∂ÊÄÅÊñáÊú¨:</strong> {{ test.details.statusText }}</p>
                      <p v-if="test.details.url"><strong>ÂÆûÈôÖURL:</strong> {{ test.details.url }}</p>
                      <p v-if="test.details.type"><strong>ÂìçÂ∫îÁ±ªÂûã:</strong> {{ test.details.type }}</p>
                      <p v-if="test.details.redirected"><strong>ÈáçÂÆöÂêë:</strong> ÊòØ</p>
                    </div>
                    <div v-else>
                      <p><strong>ÈîôËØØ‰ø°ÊÅØ:</strong> {{ test.error }}</p>
                      <p v-if="test.errorType"><strong>ÈîôËØØÁ±ªÂûã:</strong> {{ test.errorType }}</p>
                      <p v-if="test.timestamp"><strong>ÊµãËØïÊó∂Èó¥:</strong> {{ formatTime(test.timestamp) }}</p>
                    </div>
                  </template>
                </el-alert>
