            <el-tab-pane label="ËØäÊñ≠Âª∫ËÆÆ" name="recommendations" v-if="diagnosisResult.recommendations">
              <div v-for="(rec, index) in diagnosisResult.recommendations" :key="index" class="recommendation-item">
