            <el-tab-pane label="ËØ¶ÁªÜ‰ø°ÊÅØ" name="details">
              <el-collapse v-model="activeCollapse">
                <el-collapse-item title="ÊµèËßàÂô®‰ø°ÊÅØ" name="browser">
                  <pre>{{ JSON.stringify(diagnosisResult.browser, null, 2) }}</pre>
                </el-collapse-item>
                <el-collapse-item title="ÊµãËØïËØ¶ÊÉÖ" name="tests">
                  <pre>{{ JSON.stringify(diagnosisResult.tests, null, 2) }}</pre>
                </el-collapse-item>
              </el-collapse>
            </el-tab-pane>
          </el-tabs>
        </div>

        <div class="debug-actions">
          <el-button @click="clearResults">Ê∏ÖÈô§ÁªìÊûú</el-button>
          <el-button @click="exportResults">ÂØºÂá∫ÁªìÊûú</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { ElMessage } from 'element-plus'
import { Tools } from '@element-plus/icons-vue'
import { diagnoseNetworkIssues } from '@/utils/https-test'

const loading = ref(false)
const diagnosisResult = ref(null)
const activeTab = ref('browser')
const activeCollapse = ref(['browser'])

const getTestName = (key) => {
  const names = {
    basicConnection: 'Âü∫Êú¨ËøûÊé•ÊµãËØï',
    apiHealth: 'APIÂÅ•Â∫∑Ê£ÄÊü•',
    apiInfo: 'API‰ø°ÊÅØÊé•Âè£',
    localProxy: 'Êú¨Âú∞‰ª£ÁêÜÊµãËØï',
    localMockServer: 'Êú¨Âú∞Ê®°ÊãüÊúçÂä°Âô®ÊµãËØï',
    networkConnectivity: 'ÁΩëÁªúËøûÈÄöÊÄßÊµãËØï'
  }
  return names[key] || key
}

const formatTime = (timestamp) => {
  if (!timestamp) return ''
  return new Date(timestamp).toLocaleString('zh-CN')
}

const runDiagnosis = async () => {
  loading.value = true
  try {
    diagnosisResult.value = await diagnoseNetworkIssues()
    ElMessage.success('ËØäÊñ≠ÂÆåÊàê')
  } catch (error) {
    console.error('ËØäÊñ≠Â§±Ë¥•:', error)
    ElMessage.error('ËØäÊñ≠Â§±Ë¥•: ' + error.message)
  } finally {
    loading.value = false
  }
}

const clearResults = () => {
  diagnosisResult.value = null
  activeTab.value = 'browser'
}

const exportResults = () => {
  if (!diagnosisResult.value) {
    ElMessage.warning('Ê≤°ÊúâËØäÊñ≠ÁªìÊûúÂèØÂØºÂá∫')
    return
  }

  const dataStr = JSON.stringify(diagnosisResult.value, null, 2)
  const dataBlob = new Blob([dataStr], { type: 'application/json' })
  const url = URL.createObjectURL(dataBlob)
  const link = document.createElement('a')
  link.href = url
  link.download = `https-debug-${new Date().toISOString().slice(0, 19)}.json`
  link.click()
  URL.revokeObjectURL(url)
}
</script>

