import { defineStore } from 'pinia'

export const useLanguageStore = defineStore('language', {
  state: () => ({
    currentLanguage: 'zh', // ÈªòËÆ§‰∏≠Êñá
    supportedLanguages: [
      { code: 'zh', name: '‰∏≠Êñá', flag: 'üá®üá≥' },
      { code: 'en', name: 'English', flag: 'üá∫üá∏' }
    ]
  }),

  getters: {
    currentLang: (state) => state.currentLanguage,
    isChinese: (state) => state.currentLanguage === 'zh',
    isEnglish: (state) => state.currentLanguage === 'en',
    languageOptions: (state) => state.supportedLanguages
  },

  actions: {
    setLanguage(lang) {
      if (this.supportedLanguages.some(l => l.code === lang)) {
        this.currentLanguage = lang
        // ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
        localStorage.setItem('preferred-language', lang)
        // Êõ¥Êñ∞È°µÈù¢URL
        this.updateUrlLanguage(lang)
      }
    },

    initLanguage() {
      // ‰ªéURLË∑ØÂæÑÊ£ÄÊµãËØ≠Ë®Ä
      const pathLang = this.detectLanguageFromPath()
      if (pathLang) {
        this.currentLanguage = pathLang
        return
      }

      // ‰ªéÊú¨Âú∞Â≠òÂÇ®Ëé∑ÂèñËØ≠Ë®ÄÂÅèÂ•Ω
      const savedLang = localStorage.getItem('preferred-language')
      if (savedLang && this.supportedLanguages.some(l => l.code === savedLang)) {
        this.currentLanguage = savedLang
        return
      }

      // ‰ªéÊµèËßàÂô®ËØ≠Ë®ÄÊ£ÄÊµã
      const browserLang = navigator.language.split('-')[0]
      if (this.supportedLanguages.some(l => l.code === browserLang)) {
        this.currentLanguage = browserLang
      }
    },

    detectLanguageFromPath() {
      const path = window.location.pathname
      if (path.startsWith('/en/')) {
        return 'en'
      }
      return 'zh' // ÈªòËÆ§‰∏≠Êñá
    },

    updateUrlLanguage(lang) {
      const currentPath = window.location.pathname
      let newPath = currentPath

      if (lang === 'en') {
        // ÂàáÊç¢Âà∞Ëã±Êñá
        if (!currentPath.startsWith('/en/')) {
          newPath = '/en' + currentPath
        }
      } else {
        // ÂàáÊç¢Âà∞‰∏≠Êñá
        if (currentPath.startsWith('/en/')) {
          newPath = currentPath.replace('/en', '')
        }
      }

      // Êõ¥Êñ∞URL‰ΩÜ‰∏çÂà∑Êñ∞È°µÈù¢
      if (newPath !== currentPath) {
        window.history.replaceState({}, '', newPath)
      }
    },

    getLocalizedPath(path) {
      if (this.currentLanguage === 'en') {
        return `/en${path}`
      }
      return path
    },

    getCurrentPathWithoutLang() {
      const path = window.location.pathname
      if (path.startsWith('/en/')) {
        return path.replace('/en', '')
      }
      return path
    }
  }
})
