/**
 * ÊµãËØïHTTPSËøûÊé•
 * @param {string} url - Ë¶ÅÊµãËØïÁöÑURL
 * @returns {Promise<Object>} - ÊµãËØïÁªìÊûú
 */
export const testHttpsConnection = async (url) => {
  const result = {
    success: false,
    error: null,
    details: {},
    timestamp: new Date().toISOString()
  }

  try {
    console.log('ÂºÄÂßãÊµãËØïHTTPSËøûÊé•:', url)
    
    // È¶ñÂÖàÊ£ÄÊü•URLÊòØÂê¶ÊúâÊïà
    if (!url || !url.startsWith('http')) {
      throw new Error('Êó†ÊïàÁöÑURLÊ†ºÂºè')
    }

    // ‰ΩøÁî®fetchÊµãËØïËøûÊé•ÔºåÊ∑ªÂä†Ë∂ÖÊó∂ÊéßÂà∂
    const controller = new AbortController()
    const timeoutId = setTimeout(() => controller.abort(), 10000) // 10ÁßíË∂ÖÊó∂

    const response = await fetch(url, {
      method: 'GET',
      mode: 'cors',
      cache: 'no-cache',
      signal: controller.signal,
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      }
    })

    clearTimeout(timeoutId)

    result.success = response.ok
    result.details = {
      status: response.status,
      statusText: response.statusText,
      headers: Object.fromEntries(response.headers.entries()),
      url: response.url,
      redirected: response.redirected,
      type: response.type
    }

    // Â∞ùËØïËØªÂèñÂìçÂ∫îÂÜÖÂÆπÔºàÂ¶ÇÊûúÂèØËÉΩÔºâ
    try {
      const contentType = response.headers.get('content-type')
      if (contentType && contentType.includes('application/json')) {
        const data = await response.clone().json()
        result.details.responseData = data
      }
    } catch (e) {
      // ÂøΩÁï•ÂìçÂ∫îÂÜÖÂÆπËØªÂèñÈîôËØØ
    }

    console.log('HTTPSËøûÊé•ÊµãËØïÁªìÊûú:', result)
    return result

  } catch (error) {
    // ËØ¶ÁªÜÈîôËØØÂàÜÊûê
    let errorMessage = error.message
    let errorType = 'unknown'

    if (error.name === 'AbortError') {
      errorMessage = 'ËØ∑Ê±ÇË∂ÖÊó∂ (10Áßí)'
      errorType = 'timeout'
    } else if (error.message.includes('Failed to fetch')) {
      errorMessage = 'ÁΩëÁªúËøûÊé•Â§±Ë¥•ÔºåÂèØËÉΩÊòØÊúçÂä°Âô®‰∏çÂèØËææÊàñCORSÈóÆÈ¢ò'
      errorType = 'network'
    } else if (error.message.includes('Mixed Content')) {
      errorMessage = 'Ê∑∑ÂêàÂÜÖÂÆπÈîôËØØÔºöHTTPSÈ°µÈù¢Êó†Ê≥ïÂä†ËΩΩHTTPËµÑÊ∫ê'
      errorType = 'mixed_content'
    } else if (error.message.includes('CERT')) {
      errorMessage = 'SSLËØÅ‰π¶È™åËØÅÂ§±Ë¥•'
      errorType = 'ssl_certificate'
    } else if (error.message.includes('ERR_SSL')) {
      errorMessage = 'SSLËøûÊé•ÈîôËØØ'
      errorType = 'ssl_error'
    }

    result.error = errorMessage
    result.errorType = errorType
    result.details = {
      name: error.name,
      message: error.message,
      stack: error.stack,
      url: url
    }

    console.error('HTTPSËøûÊé•ÊµãËØïÂ§±Ë¥•:', error)
    return result
  }
}

/**
 * ÊµãËØïAPIÂÅ•Â∫∑Ê£ÄÊü•
 * @returns {Promise<Object>} - ÊµãËØïÁªìÊûú
 */
export const testApiHealth = async () => {
  // ‰ΩøÁî®ÂèØÁî®ÁöÑWeatherForecastÁ´ØÁÇπ‰Ωú‰∏∫ÂÅ•Â∫∑Ê£ÄÊü•
  const apiUrl = process.env.NODE_ENV === 'production' 
    ? 'https://api.openai2025.com/WeatherForecast'
    : '/api/WeatherForecast'

  return await testHttpsConnection(apiUrl)
}

/**
 * ÊµãËØïAPI‰ø°ÊÅØÊé•Âè£
 * @returns {Promise<Object>} - ÊµãËØïÁªìÊûú
 */
export const testApiInfo = async () => {
  // ‰ΩøÁî®ÂèØÁî®ÁöÑWeatherForecastÁ´ØÁÇπ‰Ωú‰∏∫‰ø°ÊÅØÊé•Âè£ÊµãËØï
  const apiUrl = process.env.NODE_ENV === 'production' 
    ? 'https://api.openai2025.com/WeatherForecast'
    : '/api/WeatherForecast'

  return await testHttpsConnection(apiUrl)
}

/**
 * Ê£ÄÊü•ÊµèËßàÂô®HTTPSÊîØÊåÅ
 * @returns {Object} - Ê£ÄÊü•ÁªìÊûú
 */
export const checkBrowserHttpsSupport = () => {
  const result = {
    isSecureContext: window.isSecureContext,
    protocol: window.location.protocol,
    userAgent: navigator.userAgent,
    features: {
      fetch: typeof fetch !== 'undefined',
      promises: typeof Promise !== 'undefined',
      cors: 'cors' in document.createElement('a'),
      webcrypto: typeof crypto !== 'undefined'
    }
  }

  console.log('ÊµèËßàÂô®HTTPSÊîØÊåÅÊ£ÄÊü•:', result)
  return result
}

/**
 * ËØäÊñ≠ÁΩëÁªúËøûÊé•ÈóÆÈ¢ò
 * @returns {Promise<Object>} - ËØäÊñ≠ÁªìÊûú
 */
export const diagnoseNetworkIssues = async () => {
  const diagnosis = {
    timestamp: new Date().toISOString(),
    browser: checkBrowserHttpsSupport(),
    environment: {
      isProduction: process.env.NODE_ENV === 'production',
      nodeEnv: process.env.NODE_ENV,
      currentUrl: window.location.href,
      protocol: window.location.protocol
    },
    tests: {}
  }

  // ÊµãËØïÂü∫Êú¨ËøûÊé•
  console.log('ÂºÄÂßãÂü∫Êú¨ËøûÊé•ÊµãËØï...')
  try {
    diagnosis.tests.basicConnection = await testHttpsConnection('https://api.openai2025.com/WeatherForecast')
  } catch (error) {
    diagnosis.tests.basicConnection = { 
      success: false, 
      error: error.message,
      errorType: 'basic_connection_error',
      timestamp: new Date().toISOString()
    }
  }

  // ÊµãËØïAPIÂÅ•Â∫∑Ê£ÄÊü•
  console.log('ÂºÄÂßãAPIÂÅ•Â∫∑Ê£ÄÊü•...')
  try {
    diagnosis.tests.apiHealth = await testApiHealth()
  } catch (error) {
    diagnosis.tests.apiHealth = { 
      success: false, 
      error: error.message,
      errorType: 'api_health_error',
      timestamp: new Date().toISOString()
    }
  }

  // ÊµãËØïAPI‰ø°ÊÅØ
  console.log('ÂºÄÂßãAPI‰ø°ÊÅØÊµãËØï...')
  try {
    diagnosis.tests.apiInfo = await testApiInfo()
  } catch (error) {
    diagnosis.tests.apiInfo = { 
      success: false, 
      error: error.message,
      errorType: 'api_info_error',
      timestamp: new Date().toISOString()
    }
  }

  // ÊµãËØïÊú¨Âú∞‰ª£ÁêÜÔºàÂºÄÂèëÁéØÂ¢ÉÔºâ
  if (!diagnosis.environment.isProduction) {
    console.log('ÂºÄÂßãÊú¨Âú∞‰ª£ÁêÜÊµãËØï...')
    try {
      diagnosis.tests.localProxy = await testHttpsConnection('/api/WeatherForecast')
    } catch (error) {
      diagnosis.tests.localProxy = { 
        success: false, 
        error: error.message,
        errorType: 'local_proxy_error',
        timestamp: new Date().toISOString()
      }
    }
  }

  // ÊµãËØïÊú¨Âú∞Ê®°ÊãüÊúçÂä°Âô®
  console.log('ÂºÄÂßãÊú¨Âú∞Ê®°ÊãüÊúçÂä°Âô®ÊµãËØï...')
  try {
    diagnosis.tests.localMockServer = await testHttpsConnection('http://localhost:3001/health')
  } catch (error) {
    diagnosis.tests.localMockServer = { 
      success: false, 
      error: error.message,
      errorType: 'local_mock_server_error',
      timestamp: new Date().toISOString()
    }
  }

  // ÊµãËØïÁΩëÁªúËøûÈÄöÊÄßÔºàpingÊµãËØïÔºâ
  console.log('ÂºÄÂßãÁΩëÁªúËøûÈÄöÊÄßÊµãËØï...')
  try {
    diagnosis.tests.networkConnectivity = await testNetworkConnectivity()
  } catch (error) {
    diagnosis.tests.networkConnectivity = { 
      success: false, 
      error: error.message,
      errorType: 'network_connectivity_error',
      timestamp: new Date().toISOString()
    }
  }

  // ÁîüÊàêËØäÊñ≠Âª∫ËÆÆ
  diagnosis.recommendations = generateRecommendations(diagnosis)

  console.log('ÁΩëÁªúËØäÊñ≠ÁªìÊûú:', diagnosis)
  return diagnosis
}

/**
 * ÊµãËØïÁΩëÁªúËøûÈÄöÊÄß
 * @returns {Promise<Object>} - ÊµãËØïÁªìÊûú
 */
export const testNetworkConnectivity = async () => {
  const result = {
    success: false,
    error: null,
    details: {},
    timestamp: new Date().toISOString()
  }

  try {
    // ÊµãËØïDNSËß£Êûê
    const testUrls = [
      'https://www.google.com',
      'https://www.baidu.com',
      'https://api.openai2025.com/WeatherForecast'
    ]

    const connectivityTests = await Promise.allSettled(
      testUrls.map(url => testHttpsConnection(url))
    )

    result.success = connectivityTests.some(test => test.status === 'fulfilled' && test.value.success)
    result.details = {
      tests: connectivityTests.map((test, index) => ({
        url: testUrls[index],
        result: test.status === 'fulfilled' ? test.value : { error: test.reason }
      }))
    }

    if (!result.success) {
      result.error = 'ÊâÄÊúâÁΩëÁªúËøûÈÄöÊÄßÊµãËØïÈÉΩÂ§±Ë¥•'
    }

  } catch (error) {
    result.error = error.message
    result.details = { error: error.message }
  }

  return result
}

/**
 * ÁîüÊàêËØäÊñ≠Âª∫ËÆÆ
 * @param {Object} diagnosis - ËØäÊñ≠ÁªìÊûú
 * @returns {Array} - Âª∫ËÆÆÂàóË°®
 */
export const generateRecommendations = (diagnosis) => {
  const recommendations = []

  // Ê£ÄÊü•ÊµèËßàÂô®ÊîØÊåÅ
  if (!diagnosis.browser.isSecureContext) {
    recommendations.push({
      type: 'warning',
      title: 'ÂÆâÂÖ®‰∏ä‰∏ãÊñáÈóÆÈ¢ò',
      message: 'ÂΩìÂâçÈ°µÈù¢‰∏çÂú®ÂÆâÂÖ®‰∏ä‰∏ãÊñá‰∏≠ÔºåÂª∫ËÆÆ‰ΩøÁî®HTTPSËÆøÈóÆ'
    })
  }

  // Ê£ÄÊü•ÂçèËÆÆ
  if (diagnosis.environment.protocol === 'http:') {
    recommendations.push({
      type: 'info',
      title: 'ÂçèËÆÆÂª∫ËÆÆ',
      message: 'ÂΩìÂâç‰ΩøÁî®HTTPÂçèËÆÆÔºåÂª∫ËÆÆÂçáÁ∫ßÂà∞HTTPS‰ª•Ëé∑ÂæóÊõ¥Â•ΩÁöÑÂÆâÂÖ®ÊÄß'
    })
  }

  // Ê£ÄÊü•APIËøûÊé•
  const apiTests = Object.values(diagnosis.tests).filter(test => 
    test.errorType && test.errorType.includes('api')
  )

  if (apiTests.length > 0) {
    recommendations.push({
      type: 'error',
      title: 'APIËøûÊé•ÈóÆÈ¢ò',
      message: 'APIÊúçÂä°Âô®ËøûÊé•Â§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊúçÂä°Âô®Áä∂ÊÄÅÂíåÁΩëÁªúËøûÊé•'
    })
  }

  // Ê£ÄÊü•SSLËØÅ‰π¶
  const sslErrors = Object.values(diagnosis.tests).filter(test => 
    test.errorType && (test.errorType.includes('ssl') || test.errorType.includes('cert'))
  )

  if (sslErrors.length > 0) {
    recommendations.push({
      type: 'error',
      title: 'SSLËØÅ‰π¶ÈóÆÈ¢ò',
      message: 'Ê£ÄÊµãÂà∞SSLËØÅ‰π¶ÈóÆÈ¢òÔºåËØ∑Ê£ÄÊü•ËØÅ‰π¶ÊòØÂê¶ÊúâÊïà'
    })
  }

  // Ê£ÄÊü•ÁΩëÁªúËøûÈÄöÊÄß
  if (diagnosis.tests.networkConnectivity && !diagnosis.tests.networkConnectivity.success) {
    recommendations.push({
      type: 'error',
      title: 'ÁΩëÁªúËøûÈÄöÊÄßÈóÆÈ¢ò',
      message: 'ÁΩëÁªúËøûÊé•ÂºÇÂ∏∏ÔºåËØ∑Ê£ÄÊü•ÁΩëÁªúËÆæÁΩÆÂíåÈò≤ÁÅ´Â¢ôÈÖçÁΩÆ'
    })
  }

  return recommendations
}
